import i18next from "i18next";
import { initReactI18next } from "react-i18next";

const i18nResources = {
  "zh-CN": {
    translation: {
      "biddingNotepad": "",
      "createTable": "",
      "create": "",
      "cancel": "",
      "search": "",
      "title": "",
      "minTricks": "",
      "minSuit": "",
      "modifyTableInfo": "",
      "OK": "",
      "removeTable": "",
      "certainToRemoveTable": "\"{{title}}\"",
      "remove": "",
      "tableDescription": "",
      "noSelectedTable": "",
      "download...": "",
      "upload...": "",
    },
  },
};

export function initI18n() {
  i18next
    .use(initReactI18next)
    .init({
      resources: i18nResources,
      lng: "zh-CN",
      fallbackLng: "zh-CN",
      interpolation: {
        escapeValue: false,
      },
    });
}